/* fira-sans-regular - latin_cyrillic-ext_cyrillic */

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local("Fira Sans Regular"),
    local("FiraSans-Regular"),
    url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.woff2") format("woff2"),
    url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.woff") format("woff"),
    url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.ttf") format("truetype"),
    url("../fonts/fira-sans-v10-latin_cyrillic-ext_cyrillic-regular.svg#FiraSans") format("svg");
}

